//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$avenir: 'AvenirNextCyr', sans-serif;

@font-face {
	@include fontdefault(normal, 300, $avenir);
	@include fontpath('AvenirNextCyr-Light', 'AvenirNextCyr-Light', AvenirNextCyr-Light); }

@font-face {
	@include fontdefault(normal, 400, $avenir);
	@include fontpath('AvenirNextCyr-Regular', 'AvenirNextCyr-Regular', AvenirNextCyr-Regular); }

@font-face {
	@include fontdefault(normal, 600, $avenir);
	@include fontpath('AvenirNextCyr-Medium', 'AvenirNextCyr-Medium', AvenirNextCyr-Medium); }

@font-face {
	@include fontdefault(normal, 700, $avenir);
	@include fontpath('AvenirNextCyr-Bold', 'AvenirNextCyr-Bold', AvenirNextCyr-Bold); }
