.slider {
  padding: 0 72px;
  font-size: 0;
  @include sm {
    padding: 0;
    margin: 0 -20px; }
  &__pause {
    display: none; }
  &__play {
    display: block; }
  &__pause,
  &__play {
    transition: opacity 0.4s; }
  &__slide {
    height: 100%;
    position: relative;
    &.is-playing {
      .slider__pause {
        display: block;
        opacity: 0; }
      .slider__play {
        display: none; }
      @include hover {
        .slider__pause {
          opacity: 1; } } } }
  &__next,
  &__prev {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }
  &__next {
    right: 36px; }
  &__prev {
    &__next {}
    left: 36px; }
  &__video-buttons {
    @include vcenter;
    z-index: 1000;
    font-size: 0;
    .video-btn {
      display: block; } }
  &__video {
    @include coverdiv;
    object-fit: cover;
    background-color: $white;
    display: block; }
  &__pause {
    display: none; } }

.slider-wrapper {
  position: relative; }

.slider-btn {
  display: inline-block;
  color: $green;
  background-color: transparent;
  .icon {
    font-size: 24px; } }

.video-btn {
  display: inline-block;
  height: 110px;
  background-color: transparent;
  @include xl {
    height: 80px; }
  @include sm {
    height: 36px; }
  svg {
    height: 100%; } }

.slick-dots {
  display: flex;
  justify-content: center;
  padding: 28px 0;
  li {
    padding: 0 6px;
    button {
      display: block;
      height: 10px;
      width: 10px;
      border-radius: 50%;
      border: 1px solid $green;
      background-color: transparent; }
    &.slick-active {
      button {
        background-color: $green; } } }
  button {
    font-size: 0; } }

.video-hint {
  position: absolute;
  left: 50%1;
  top: calc(100% + 3px);
  transform: translateX(-50%);
  color: #fff;
  font-size: 13px;
  opacity: 0;
  visibility: hidden;
  transition: visility .4s,opacity .4s; }

.slick-slide {
  & > div {
    height: 40vw;
    @include sm {
      height: 52vw; } } }
