.descr-section {
  margin-bottom: 65px;
  @include sm {
    margin-bottom: 40px; }
  &__top {
    padding: 68px 0;
    @include sm {
      padding: 28px 0; }
    p {
      max-width: 900px;
      margin: 0 auto;
      text-align: center; } }
  &__image {
    margin-bottom: 72px;
    @include xl {
      margin-bottom: 50px; }
    @include sm {
      margin-left: -20px;
      margin-right: -20px;
      margin-bottom: 32px; }
    img {
      display: block;
      max-width: 100%; } }
  &__bottom {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  &__bottom-block {
    @include fsize(calc(50% - 66px));
    @include xl {
      @include fsize(calc(50% - 40px)); }
    @include sm {
      @include fsize(100%); }
    p {
      &:not(:last-child) {
        margin-bottom: 1.25em; } }
    &:first-child {
      @include sm {
        margin-bottom: 3em; } } } }
