.slider__slide { height: 100%; position: relative; }

.slider__slide.is-playing .slider__pause { display: block; opacity: 0; }

.slider__slide.is-playing .slider__play { display: none; }

.no-touch .slider__slide.is-playing:hover .slider__pause { opacity: 1; }

.slider__next, .slider__prev { position: absolute; top: 50%; transform: translateY(-50%); }

.slider__next { right: 36px; }

.slider__prev { left: 36px; }

